import popper from "popper.js";
import bootstrap from "bootstrap";
// import './scrolloverflow';
// import 'fullpage.js/dist/jquery.fullpage.extensions.min';
import 'slick-carousel';
import validate from 'jquery-validation';
import Inputmask from 'inputmask';

jQuery(function () {

    /* Sticky nav */
    let navTop = $('.header');

    if (navTop.length) {
        let stickyNavTop = navTop.offset().top;

        let stickyNav = function () {

            let scrollTop = $(window).scrollTop();
            if (scrollTop > 0) {
                $('.header').addClass('scroll');
            } else {
                $('.header').removeClass('scroll');
            }
        };

        stickyNav();
        $(window).scroll(function () {
            stickyNav();
        });
    }
    /* Sticky nav */

    /*
  * Replace all SVG images with inline SVG
  */
    jQuery('img.svg').each(function () {
        var $img = jQuery(this);
        var imgID = $img.attr('id');
        var imgClass = $img.attr('class');
        var imgURL = $img.attr('src');

        jQuery.get(imgURL, function (data) {
            // Get the SVG tag, ignore the rest
            var $svg = jQuery(data).find('svg');

            // Add replaced image's ID to the new SVG
            if (typeof imgID !== 'undefined') {
                $svg = $svg.attr('id', imgID);
            }
            // Add replaced image's classes to the new SVG
            if (typeof imgClass !== 'undefined') {
                $svg = $svg.attr('class', imgClass + ' replaced-svg');
            }

            // Remove any invalid XML tags as per http://validator.w3.org
            $svg = $svg.removeAttr('xmlns:a');

            // Replace image with new SVG
            $img.replaceWith($svg);

        }, 'xml');

    });


    $('.collection__item').hover(function (e) {
        $(this).find('.description').stop().slideToggle(300);
    }, function (e) {
        $(this).find('.description').stop().slideToggle(300);
    });

    $('#carousel-brand').slick({
        centerPadding: 0,
        slidesToShow: 3,
        slidesToScroll: 1,
        infinite: true,
        centerMode: false,
        variableWidth: false,
        prevArrow: '.brand-arrow__prev',
        nextArrow: '.brand-arrow__next',
        responsive: [
            {
                breakpoint: 0,
                settings: {
                    slidesToShow: 1
                }
            },
            {
                breakpoint: 575.98,
                settings: {
                    slidesToShow: 1,
                    arrows: false,
                }
            },
            {
                breakpoint: 767.98,
                settings: {
                    slidesToShow: 2,
                    arrows: false,
                }
            },
        ]
    });


    $('.collection .more-link a').on('click', function (e) {
        e.preventDefault();
        let id = $(this).data('collection');

        $.ajax({
            type: "GET",
            url: "data/collections.json",
            contentType: 'application/json',
            dataType: 'json',
            data: '',
            success: function (data) {
                data.forEach(function (obj) {
                    if (obj.id == id) {

                        $('#collectionModal').find('.collection-modal__content h2').html(obj.content[0].title);
                        $('#collectionModal').find('.collection-modal__content .text').html(obj.content[0].text);

                        let products = '';
                        obj.products.forEach(function (product) {
                            let img = product.img ? '<div class="img"><img src="' + product.img + '" alt="' + product.title + '"></div>' : '',
                                title = product.title ? '<div class="title">' + product.title + '</div>' : '',
                                size = product.size ? '<span>Размер: ' + product.size + '</span>' : '',
                                thickness = product.thickness ? '<span>Толщина: ' + product.thickness + '</span>' : '',
                                art = product.art ? '<div class="art">Артикул: ' + product.art + '</div>' : '',
                                new_price = product.new_price ? '<div class="price__new">' + product.new_price + '</div>' : '',
                                price = product.price ? '<div class="price__old">' + product.price + '</div>' : '';

                            products += '<div class="products__item">' + img + title + '<div class="specifications">' + size + thickness + '</div>' + art + '<div class="price">' + new_price + price + '</div></div>';
                        });

                        $('#carousel-products').html();
                        $('#carousel-products').html(products)

                        $('#collectionModal').modal('show')

                    }
                });
            },
            error: function (jqXHR, textStatus, errorThrown) {
                console.log(textStatus)
            }
        });
    });

    function productCarousel() {
        $('#carousel-products').slick({
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            prevArrow: '.product-prev',
            nextArrow: '.product-next',
            // adaptiveHeight: true
        });
        setTimeout(function (e) {
            $('.collection-modal__products').css({'opacity': '1', 'height': 'auto'})
        })
    }

    function destroyProductCarousel() {
        if ($('#carousel-products').hasClass('slick-initialized')) {
            $('#carousel-products').slick('destroy');
        }
    }

    $('#collectionModal').on('shown.bs.modal', function (e) {
        destroyProductCarousel();
        productCarousel();
    });
    $('#collectionModal').on('hidden.bs.modal', function (e) {
        destroyProductCarousel();
        $('.collection-modal__products').css({'opacity': '0', 'height': '0'});
        $(".collection-modal__content .text").removeClass("show-more");
        $(".readMore").text('Читать больше')
    });


    $(".readMore").on('click', function (e) {
        e.preventDefault();

        if($(".collection-modal__content .text").hasClass("show-more")) {
            $(this).text("Читать больше");
        } else {
            $(this).text("Свернуть описание");
        }

        $(".collection-modal__content .text").toggleClass("show-more");
    });


    $('.btn-calc').on('click', function (e) {
        e.preventDefault();
        if ( $('#collectionModal').hasClass('show') ) {
            $('#collectionModal').modal('hide');

            setTimeout(function (e) {
                $('#callbackModal').modal('show');
            },600)
        }else{
            $('#callbackModal').modal('show');
        }
    })

    $('.collection>div').slice(0,8).show();

    $('.show-more-collection').on('click', function (e) {
        e.preventDefault();

        if($(".collection").hasClass("show")) {
            $(this).text("Показать еще 12 коллекций");
        } else {
            $(this).text("Свернуть");
        }

        $(".collection").toggleClass("show");
        $('.collection>div').slice(8, 20).slideToggle();
    });


    Inputmask({"mask": "+375-99-999-99-99"}).mask(document.querySelectorAll('[name="phone"]'));


    $('#callBackForm').validate({
        rules: {
            email: {
                email: true
            }
        },
        messages: {
            name: {
                required: function(status, element) {
                    if ($(element).data('message')){
                        return $(element).data('message')
                    }else{
                        return 'Введите Ваше имя!'
                    }
                }
            },
            phone: {
                required: function(status, element) {
                    if ($(element).data('message')){
                        return $(element).data('message')
                    }else{
                        return 'Введите Ваш телефон!'
                    }
                }
            },
            email: {
                required: function(status, element) {
                    if ($(element).data('message')){
                        return $(element).data('message')
                    }else{
                        return 'Введите Ваш e-mail!'
                    }
                },
                email: function (status, element) {
                    if ($(element).data('message-email')){
                        return $(element).data('message-email')
                    }else{
                        return 'Пожалуйста, введите действительный адрес электронной почты!'
                    }
                }
            }
        },
        highlight: function (element, errorClass, validClass) {
            if ($(element).attr('type') !== 'radio') {
                $(element).addClass('is-invalid');
            }
        },
        unhighlight: function (element, errorClass, validClass) {
            $(element).removeClass('is-invalid');
        },
        errorClass: 'invalid-feedback'
    });


    /* Отправка формы */

    $('form').submit(function () {
        let form = $(this);

        if (form.valid()) {
            $.ajax({
                type: 'POST',
                processData: true,
                contentType: 'application/x-www-form-urlencoded',
                url: 'sendmail.php',
                data: form.serialize(),
                success: function (data) {
                    if (data['error']) {
                        alert(data['error']);
                    } else {
                        form.closest('.modal').modal('hide');

                        setTimeout(function () {
                            $('#thxModal').modal('show');
                            $('#thxModal').on('shown.bs.modal', function (e) {
                                setTimeout(function () {
                                    $('#thxModal').modal('hide');
                                }, 1500);
                            })

                        }, 500);

                        form[0].reset();
                    }
                },
                error: function (xhr, ajaxOptions, thrownError) {
                    alert(xhr.status);
                    alert(thrownError);
                },
                complete: function (data) {
                    form.find('button').prop('disabled', false);
                }
            });
        }
        return false;
    });

    /* Отправка формы */

});
